<template>
<div id="loading" :class="{'animate__animated animate__zoomOut' : !isshow }" v-if="isenter" @touchmove.prevent @mousewheel.prevent>
  <div class="surf left bac" ref="left"></div>
  <div class="surf right bac" ref="right"></div>
  <div class="loading-center" v-if="isshow">
    <p  class="loading-fli" :style="{'opacity': fli}">Loading . . .</p>
    <div id="loading-box">
			<svg  xmlns="http://www.w3.org/2000/svg" width="100%" height="100%">
				<circle class="bg" 
				  cx="100" 
				  cy="100" 
				  r="80" 
				  stroke="#ffea00" 
				  stroke-width="9" 
				  fill="none" 
          stroke-linecap="round"/>
				
				<circle class="loading-cir"
          id="loadingCir"
          cx="100" 
          cy="100" 
          r="80" 
          stroke="rgb(23, 40, 77)" 
          stroke-linecap="round"
          stroke-width="11" 
          :style="{'stroke-dashoffset': strokeDashoffset}"
          fill="none" />
			</svg>

			<svg :class="{'dospin': dospin}" t="1603781387539" id="spin" class="icon spin" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5155" width="100" height="100">
				<path d="M808.986483 254.142188c0-243.909126-284.562011-243.909126-284.562011-243.909126l0 487.817228C524.426519 498.049267 808.986483 498.049267 808.986483 254.142188L808.986483 254.142188z" p-id="5156" fill="#ffea00"></path>
				<path d="M254.642073 213.488279c-243.909126 0-243.909126 284.560988-243.909126 284.560988l487.817228 0C498.549152 498.049267 498.549152 213.488279 254.642073 213.488279L254.642073 213.488279z" p-id="5157" fill="#ffea00"></path>
				<path d="M213.989187 767.835759c0 243.908102 284.560988 243.908102 284.560988 243.908102L498.550175 523.924587C498.549152 523.927657 213.989187 523.927657 213.989187 767.835759L213.989187 767.835759z" p-id="5158" fill="#ffea00"></path>
				<path d="M768.334621 808.486598c243.908102 0 243.908102-284.562011 243.908102-284.562011L524.423449 523.924587C524.426519 523.927657 524.426519 808.486598 768.334621 808.486598L768.334621 808.486598z" p-id="5159" fill="#ffea00"></path>
			</svg>
		</div>
    <p>{{current}}%</p>
  </div>
</div>
</template>

<script >

// import load from '../js/TheLoading';

export default {
  data() {
    return {
      isshow: true,
      isenter: true,
      current: 0,
      strokeDashoffset: '502',
      dospin: false,
      isloading: true,
    };
  },
  mounted() {
    this.open()
    this.timerAnimate()
    this.timerWord()
  },
  computed: {
    index() {
      return Math.floor(Math.random() * 3)
    },
    fli(){
      return this.isloading ? 1 : 0
    }
  },
  methods: {
    // 关闭加载界面
    open() {
      setTimeout(() => {
        this.isshow = false
      }, 6000);
      setTimeout(() => {
        this.isenter = false
      }, 6500);
    },
    // 中间的动画
    timerAnimate(){
      setTimeout(() => {
				this.strokeDashoffset = '0'
        setTimeout(() => {
          this.dospin = true
            setTimeout(() => {
              this.strokeDashoffset = '-502'
            },2000)
        },1000)
      },1000)
    },
    // 文字的变化
    timerWord(){
      let timerCurrent= setInterval(() => {
        this.current >= 100 ? clearInterval(timerCurrent) : this.current += 1
      }, 50)

      let timerLoad = setInterval(() => {
        this.current >= 100 ? clearInterval(timerLoad) : this.isloading = !this.isloading
      }, 600);
    }
  },
};
</script>

<style >
.loading-fli{
  transition: all 1s;
}
.dospin{
  transform: rotate(-720deg);
}
.loading-center{
  position: absolute;
  width: 400px;
  height: 600px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.loading-center > p {
  font: 25px/50px "微软雅黑";
  color: #ffea00;
  font-weight: bold;
}
#loading-box{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 200px;
  height: 200px;
  background-color: transparent;
}
.loading-cir{
  stroke-dasharray: 502 502;
  transition: all linear 1s;
}
.spin{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  transition: all  2s;
}

#loading{
  position: absolute;
  background-color: rgb(23, 40, 77);
  width: 100%;
  height: 100%;
  z-index: 10;
}
#loading::after{
  position: absolute;
  content: '';
  background-image: radial-gradient(transparent,  black);
  width: 100%;
  height: 100%;
  z-index: 11;
}

</style>



